	module.exports = {"data":{"site":{"siteMetadata":{"title":"Annalisa Valente"}},"contentfulPageSelectorPanel":{"pages":[{"name":"PD Spellbook","title":"PD Spellbook","slug":"/projects/pd-spellbook","description":"Glossary of terms for product people, side project"},{"name":"Google","title":"Google","slug":"/projects/google/","description":"Interaction designer contractor in the Google Play Console team"},{"name":"Local Cancer Intelligence","title":"Local Cancer Intelligence","slug":"/projects/local-cancer-intelligence/","description":"New filter system for the LCI website"},{"name":"The Telegraph Puzzle ","title":null,"slug":"/projects/the-telegraph-puzzle/","description":"Redesign the experience of the Telegraph Puzzle website"},{"name":"Mindfulness Moments","title":"Mindfulness Moments","slug":"/projects/mindfulness-moments/","description":"Building a new fundraising proposition"},{"name":"Macmillan Cancer Support","title":null,"slug":"/projects/brave-the-shave/","description":"Redesigning Brave the Shave microsite"},{"name":"The Telegraph","title":"The Telegraph","slug":"/projects/the-telegraph/","description":"Working on various digital newspaper touch points"},{"name":"Nectar ","title":"Nectar","slug":"/projects/nectar/","description":"  New way of discovering Nectar brands for web and iOS-Android "},{"name":"MyMail & Nectar","title":"MyMail & Nectar","slug":"/projects/mymail-and-nectar/","description":"Launch of MyMail and Nectar proposition"},{"name":"Mighty hikes","title":null,"slug":"/projects/mighty-hikes","description":"Discovering phase of a fundraising proposition"},{"name":"Calvin Klein","title":"Calvin Klein","slug":"/projects/calvin-klein/","description":"Designing microsite for Calvin Klein Italy"},{"name":"Remember¬†names","title":null,"slug":"/projects/face-name/","description":"Concept of an Android experience that helps to match faces to names"},{"name":"Emoji and depression","title":null,"slug":"/projects/emoji-and-depression","description":"Detect depression in young people "}]},"contentfulPage":{"slug":"/about/","password":null,"panels":[{"__typename":"ContentfulImagePanel","fullWidth":false,"media":null},{"__typename":"ContentfulContentPanel","content":{"content":"## My approach as a Designer\n\n__üî≠User Research:__\nI discover and reasearch in order to understand users' needs. I like to ask questions and to analyse data. From creating \"Personas\" and User Journeys to empathy map, I employ a design thinking approach to best meet user's goals.\n\n__üö∏User Flows and Wireframes:__\nI combine the research findings, requirements gathering, customer mapping and workshops decisions into something more tangible. I work on the initial drafts and revise them, considering also design and technical requirements with development team and stakeholders.\n\n__üöÄInteractive Prototypes:__\nI'm able to create interacions and behaviours, translating them into interactive prototypes ready for stakeholders and, more important for the users.\n\n__üèóDesign System:__\nI create UI delivelables and I like working using design system. I help teams to be more agile using and creating reusable components that can be used across platforms in order to save designers and devs time.\n\n__üî¨Testing, Refining, Iterating:__\nMy commitment continues with preparing, moderating and reviewing user testing, then working close to developers sprint after sprint, and monitoring and improving the UX and UI after the launch.\n\n## My approach as a Lead\n\n__üö¶UX Strategy and KPIs:__\nBusiness requirements and outcomes are as important as users needs and goals. I help companies and teams to define KPIs in order to achieve business goals and meeting user's needs.\n\n__üó∫Product management:__\nI bring with me many years of design experience in various industries and this allow me to be confortable to work with senior stakeholders in order to identify opportunities, create, prioritise and shape roadmaps.\n\n__üëÅDesign critique:__\nI run design critique with design teams to help to analysing designs giving feedback on whether it meets its objectives.\n\n__üë•Mentoring:__\nI'm happy working with juniors and I like helping them to grow as designers. Outside work I'm the Events Support Lead at <a href=\"https://uxpa-uk.org/\" target=\"_blank\" title=\"External link to UXPA UK\">UXPA UK</a> and I help promoting user centered design and supporting UX practitioners.\n\n__üó£Communication:__\nI believe that communication and decision making are the key to making a great team and to build great products.\nI have a high value for openness, humility, flexibility, collaboration, diversity and empathy.\n\n\n\n__My outside work interests:__\n\nIn my free time I like playing D&D, board games and learning drums.\n\nI am a member of the Jury at <a href=\"https://www.cssdesignawards.com/\" target=\"_blank\" title=\"External link to CSS Design Awards\">www.cssdesignawards.com</a> and sometimes I like to write articles and to participate at hackathons. \n","childMarkdownRemark":{"html":"<h2>My approach as a Designer</h2>\n<p><strong>üî≠User Research:</strong>\nI discover and reasearch in order to understand users' needs. I like to ask questions and to analyse data. From creating \"Personas\" and User Journeys to empathy map, I employ a design thinking approach to best meet user's goals.</p>\n<p><strong>üö∏User Flows and Wireframes:</strong>\nI combine the research findings, requirements gathering, customer mapping and workshops decisions into something more tangible. I work on the initial drafts and revise them, considering also design and technical requirements with development team and stakeholders.</p>\n<p><strong>üöÄInteractive Prototypes:</strong>\nI'm able to create interacions and behaviours, translating them into interactive prototypes ready for stakeholders and, more important for the users.</p>\n<p><strong>üèóDesign System:</strong>\nI create UI delivelables and I like working using design system. I help teams to be more agile using and creating reusable components that can be used across platforms in order to save designers and devs time.</p>\n<p><strong>üî¨Testing, Refining, Iterating:</strong>\nMy commitment continues with preparing, moderating and reviewing user testing, then working close to developers sprint after sprint, and monitoring and improving the UX and UI after the launch.</p>\n<h2>My approach as a Lead</h2>\n<p><strong>üö¶UX Strategy and KPIs:</strong>\nBusiness requirements and outcomes are as important as users needs and goals. I help companies and teams to define KPIs in order to achieve business goals and meeting user's needs.</p>\n<p><strong>üó∫Product management:</strong>\nI bring with me many years of design experience in various industries and this allow me to be confortable to work with senior stakeholders in order to identify opportunities, create, prioritise and shape roadmaps.</p>\n<p><strong>üëÅDesign critique:</strong>\nI run design critique with design teams to help to analysing designs giving feedback on whether it meets its objectives.</p>\n<p><strong>üë•Mentoring:</strong>\nI'm happy working with juniors and I like helping them to grow as designers. Outside work I'm the Events Support Lead at <a href=\"https://uxpa-uk.org/\" target=\"_blank\" title=\"External link to UXPA UK\">UXPA UK</a> and I help promoting user centered design and supporting UX practitioners.</p>\n<p><strong>üó£Communication:</strong>\nI believe that communication and decision making are the key to making a great team and to build great products.\nI have a high value for openness, humility, flexibility, collaboration, diversity and empathy.</p>\n<p><strong>My outside work interests:</strong></p>\n<p>In my free time I like playing D&#x26;D, board games and learning drums.</p>\n<p>I am a member of the Jury at <a href=\"https://www.cssdesignawards.com/\" target=\"_blank\" title=\"External link to CSS Design Awards\">www.cssdesignawards.com</a> and sometimes I like to write articles and to participate at hackathons. </p>"}}}]}},"pathContext":{"slug":"/about/","p":"35675e68f4b5af7b995d9205ad0fc43842f16450"}}

